import http from '../http-common.js';

class UserDataService {
    createUser(data) {
        return http.post('/', data);
    }

    findUser(email, user) {
        console.log("THE QUERY: " + email + " USER: " + user);
        return http.get('/', {
                body: { email_address: email,
                        username: user }
        });
    }
}

export default new UserDataService();